<template>
  <div class="header">
    <ul class="header-button-left">
      <li v-if="step != 0" @click="step--">Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish()">추가</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <Container
    :R_data="R_data"
    :step="step"
    :addImg="addImg"
    @write="작성한글 = $event"
  ></Container>

  <!-- <button @click="more">더보기</button> -->

  <div class="footer">
    <ul class="footer-button-plus">
      <input
        @change="upload"
        accept="image/*"
        type="file"
        id="file"
        class="inputfile"
      />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>
</template>

<script>
import Container from "./components/Container.vue";
import D_data from "./assets/Data";
import axios from "axios";

export default {
  name: "App",
  data() {
    return {
      R_data: D_data,
      btCnt: 0,
      step: 0,
      addImg: null,
      작성한글: "",
    };
  },
  components: {
    Container: Container,
  },
  methods: {
    more() {
      if (this.btCnt == 0) {
        axios
          .get("https://codingapple1.github.io/vue/more0.json")
          .then((aa) => {
            console.log(aa.data);
            this.R_data.push(aa.data);
            this.btCnt++;
          });
      } else {
        axios
          .get("https://codingapple1.github.io/vue/more1.json")
          .then((aa) => {
            console.log(aa.data);
            this.R_data.push(aa.data);
            this.btCnt++;
          });
      }
    },

    upload(e) {
      let 파일 = e.target.files;
      console.log(파일[0]);
      let url = URL.createObjectURL(파일[0]);
      console.log(url);
      this.addImg = url;
      this.step++;
    },

    publish() {
      var 내게시물 = {
        name: "Kim Hyun",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.addImg,
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.작성한글,
        filter: "perpetua",
      };

      this.R_data.unshift(내게시물);
      this.step = 0;
    },
  },
};
</script>

